.apps-page .shared-get-started{
    margin:0 !important;
}
.apps-page .browsers-row{
    margin:7.5% 0 5%;
    padding:0 10%;
    h1{
        font-size: 35px;
        text-align: center;
    }
    p{
        font-size: 18px;
        margin:15px auto 35px;
        width: 70%;
        text-align: center;
    }
    .items{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .item{
        width: 18%;
        text-align: center;
    }
    .img{
        height: 85px;
        margin:0 auto;
    }
    h6{
        font-size: 18px;
        margin:20px 0;
    }
    button{
        background-color: #427CFF;
        color:#fff;
        border: 0;
        padding:8px 30px;
        border-radius: 20px;
        font-size: 17px;
        cursor: pointer;
    }
}

.apps-page .last-row{
    margin:5% 0 0;
    padding:2.5% 7.5%;
    text-align: center;
    background-color: #f7f7f7;
    .img{
        content: url('../../../../assets/Apps/pc.png');
        width: 20%;
        margin:0 auto;
    }
    h6{
        font-size: 20px;
        margin:30px 0 25px;
    }
    p{
        font-size: 18px;
        margin:5px 0;
        a{
            color:#427CFF;
        }
    }
}

@media(max-width:1401px){
    .apps-page .browsers-row{
        h1{
            font-size: 30px;
        }
        p{
            font-size: 15px;
            margin:15px auto 30px;
        }
        .img{
            height: 60px;
        }
        h6{
            font-size: 15px;
            margin:20px 0;
        }
        button{
            padding:8px 30px;
            border-radius: 20px;
            font-size: 14px;
        }
    }
    
    .apps-page .last-row{
        h6{
            font-size: 16.5px;
            margin:25px 0 20px;
        }
        p{
            font-size: 15px;
        }
    }
}

@media(max-width:767px){
    .apps-page .browsers-row{
        margin:35px 0;
        padding:0 7.5%;
        h1{
            font-size: 20px;
        }
        p{
            font-size: 14px;
            margin:10px auto 25px;
            width: 100%;
        }
        .items{
            justify-content: center;
        }
        .item{
            width: 110px;
            margin:15px 10px;
        }
        .img{
            height: 55px;
        }
        h6{
            font-size: 15px;
            margin:15px 0;
        }
    }
    
    .apps-page .last-row{
        margin:35px 0 0;
        padding:30px 7.5%;
        .img{
            width: 220px;
            max-width: 100%;
        }
        h6{
            font-size: 15px;
        }
        p{
            font-size: 14px;
            margin:3px 0;
        }
    }
}

.apps-page .browsers-row{
    button{
        display: inline-block;
        vertical-align: middle;
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        position: relative;
        -webkit-transition-property: color;
        transition-property: color;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
    }
    button:before {
        content: "";
        position: absolute;
        z-index: -1;
        top: -1px;
        left: -1px;
        right: -1px;
        bottom: -1px;
        background: #fff;
        -webkit-transform: scale(0);
        transform: scale(0);
        -webkit-transition-property: transform;
        transition-property: transform;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
        border-radius: 20px;
    }
    button:hover, button:focus, button:active {
        color: #427CFF;
    }
    button:hover:before, button:focus:before, button:active:before {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}