.apps-page .apps-tab{
    padding:0 10%;
    margin-top: -75px;
}
.apps-page .apps-tab .react-tabs__tab-list{
    display: flex;
    justify-content: space-between;
    border:1px solid #ddd;
    background-color: #fff;
    border-radius: 10px;
    padding:35px;
    margin-bottom: 50px;
    .react-tabs__tab{
        width: 13%;
        text-align: center;
        cursor: pointer;
        svg{
            font-size: 50px;
            color:#999;
            margin-bottom: 10px;
        }
        p{
            font-size: 18px;
        }
        &:hover svg{
            color:#427CFF;
        }
    }
    .react-tabs__tab--selected svg{
        color:#427CFF;
    }
}

.apps-page .apps-tab .react-tabs__tab-panel{
    text-align: center;
    .download{
        button{
            display: flex;
            align-items: center;
            width: fit-content;
            margin:0 auto;
            background-color: #427CFF;
            border:0;
            color:#fff;
            font-size: 18px;
            padding-right: 25px;
            border-radius: 28px;
            cursor: pointer;
        }
        .icon{
            font-size: 30px;
            width: 56px;
            height: 56px;
            border-radius: 28px;
            margin-right: 10px;
            background-color: #1159ff;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        p{
            font-size: 18px;
            margin-top: 20px;
            a{
                color:#427CFF;
            }
        }
    }
}

@media(max-width:1401px){
    .apps-page .apps-tab{
        margin-top: -45px;
    }
    .apps-page .apps-tab .react-tabs__tab-list{
        padding:30px;
        margin-bottom: 40px;
        .react-tabs__tab{
            svg{
                font-size: 40px;
                margin-bottom: 7.5px;
            }
            p{
                font-size: 15px;
            }
        }
    }
    
    .apps-page .apps-tab .react-tabs__tab-panel{
        .download{
            button{
                font-size: 15px;
                padding-right: 20px;
                border-radius: 23px;
            }
            .icon{
                font-size: 25px;
                width: 46px;
                height: 46px;
                border-radius: 23px;
                margin-right: 10px;
            }
            p{
                font-size: 15px;
                margin-top: 20px;
            }
        }
    }
}

@media(max-width:900px){
    .apps-page .apps-tab{
        padding:0 7.5%;
        margin-top: -35px;
    }
}

@media(max-width:767px){
    .apps-page .apps-tab{
        margin-top: -40px;
    }
    .apps-page .apps-tab .react-tabs__tab-list{
        flex-wrap: wrap;
        justify-content: center;
        padding:35px;
        margin-bottom: 30px;
        .react-tabs__tab{
            width: 65px;
            margin:10px;
            svg{
                font-size: 30px;
                margin-bottom: 5px;
            }
            p{
                font-size: 14px;
            }
        }
    }
    
    .apps-page .apps-tab .react-tabs__tab-panel{
        .download{
            button{
                font-size: 14px;
                padding-right: 15px;
                border-radius: 20px;
            }
            .icon{
                font-size: 20px;
                width: 40px;
                height: 40px;
                border-radius: 20px;
                margin-right: 10px;
            }
            p{
                font-size: 14px;
                margin-top: 15px;
            }
        }
    }
}

//=============================================
.apps-page .security-row{
    margin:5% 0;
    padding:0 10%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .col1{
        width: 40%;
        h1{
            font-size: 35px;
            margin-bottom: 45px;
        }
        .item{
            display: flex;
            align-items: center;
            margin:20px 0;
            padding:10px 15px;
            border-radius: 7.5px;
            border:1px solid transparent;
            &:hover{
                border:1px solid #ddd;
                .icon{
                    background-color: #427CFF;
                    color:white;
                }
            }
        }
        .icon{
            width: 70px;
            height: 70px;
            margin-right: 20px;
            font-size: 40px;
            border-radius: 50%;
            color:#444;
            background-color: #eee;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        p{
            flex: 1;
            font-size: 17px;
        }
    }
}

.apps-page .security-row .col2{
    position: relative;
    width: 40%;
    .bg-img{
        width: 100%;
        border-radius: 10px;
        content: url('../../../../assets/Apps/bg-img.png');
    }
    form{
        background-color: white;
        border:1px solid #eee;
        border-radius: 10px;
        position: absolute;
        bottom:-15%;
        width: 70%;
        left:0;
        right: 0;
        margin-left: auto;
        margin-right: auto;
        padding:25px;
    }
    .circles{
        display: flex;
        .circle{
            width: 15px;
            height: 15px;
            border-radius: 50%;
            background-color: #ddd;
            margin:0 7.5px;
        }
    }
    .logo{
        content: url('../../../../assets/Navbar/blue-logo.png');
        width: 50px;
        margin:15px auto 25px;
    }
    h6{
        font-size: 18px;
        text-align: center;
    }
    .input-content{
        display: flex;
        align-items: center;
        border:1px solid #ccc;
        border-radius: 25px;
        padding:5px 15px;
        margin:15px 0;
        span{
            font-size: 18px;
            color:#888;
            margin-right: 10px;
            svg{
            margin-bottom: -2px;
            }
        }
        input{
            flex: 1;
            height: 30px;
            font-size: 14px;
            border:0;
            &:focus{
                outline: none;
            }
        }
    }
    button{
        background-color: #427CFF;;
        color:white;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        font-size: 16px;
        width: 100%;
        border:0;
        height: 40px;
        border-radius: 20px;
    }
}

@media(max-width:1401px){
    .apps-page .security-row{
        .col1{
            width: 40%;
            h1{
                font-size: 30px;
                margin-bottom: 30px;
            }
            .item{
                margin:10px 0;
            }
            .icon{
                width: 60px;
                height: 60px;
                margin-right: 15px;
                font-size: 30px;
            }
            p{
                font-size: 14px;
            }
        }
    }
    
    .apps-page .security-row .col2{
        width: 40%;
        form{
            bottom:-15%;
            width: 70%;
            padding:20px;
        }
        .circles{
            .circle{
                width: 12px;
                height: 12px;
            }
        }
        .logo{
            width: 40px;
            margin:15px auto 25px;
        }
        h6{
            font-size: 15px;
        }
        .input-content{
            border-radius: 25px;
            padding:5px 15px;
            margin:15px 0;
            span{
                font-size: 15px;
                margin-right: 7.5px;
                svg{
                margin-bottom: -1px;
                }
            }
            input{
                height: 22.5px;
                font-size: 12px;
            }
        }
        button{
            font-size: 14px;
            height: 35px;
            border-radius: 20px;
        }
    }
}

@media(max-width:1050px){
    .apps-page .security-row{
        margin:7.5% 0;
        padding:0 7.5%;
        .col1{
            width: 42.5%;
        }
        .col2{
            width: 50%;
        }
    }
}

@media(max-width:800px){
    .apps-page .security-row{
        margin:35px 0;
        display: block;
        .col1{
            width: 100%;
            margin:25px 0;
            h1{
                font-size: 20px;
                margin-bottom: 15px;
            }
            .item{
                margin:5px 0;
            }
            .icon{
                width: 50px;
                height: 50px;
                margin-right: 15px;
                font-size: 25px;
            }
            p{
                font-size: 14px;
            }
        }
    }
    
    .apps-page .security-row .col2{
        width: 400px;
        max-width: 100%;
        margin:45px auto;
        form{
            bottom:-15%;
            width: 80%;
            padding:15px;
        }
        .circles{
            .circle{
                width: 10px;
                height: 10px;
                margin:0 5px;
            }
        }
        .logo{
            width: 35px;
            margin:10px auto 20px;
        }
        h6{
            font-size: 14px;
        }
    }
}

@media(max-width:345px){
    .apps-page .security-row .col2{

        margin:45px auto;
        form{
            bottom:-30px;
            width: 90%;
            padding:10px;
        }      
    }
}