.apps-page{
    padding-top: 70px;
    overflow: hidden;
    .nav{
        background-color: #fff;
        border-bottom: 1px solid #ccc;

        .nav-logo{
            .logo-container{
                content: url('../../../../assets/Navbar/blue-logo.png') !important;
            }
            h6{
                color:#111 !important;
            }
        }
        .nav-links{
            .inner-nav-link{
                color:#000 !important;
                svg{
                    color:#444;
                }
            }
        }
        .nav-auth{
            .nav-login a{
                color:#000 !important;
            }
        }
        .apps-link .inner-nav-link{
            color: #427CFF !important;
        }
    }
    .nav__sticky{
        border:0;
    }
    .mobile-toolbar{
        background-color: white;
        border-bottom: 1px solid #ccc;
        transition: 0.2s ease;
        .mobile-logo-row{
            h6{
                color:#333 !important;
            }
        }
        button{
            color:#222 !important;
        }
    }
    .toolbar__sticky{
        border: 0;
    }
}

.apps-page .banner{
    padding:7.5% 25%;
    background-image: url('../../../../assets/Apps/banner.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    text-align: center;
    h1{
        font-size: 40px;
        color:#fff;
    }
    p{
        font-size: 18px;
        color:#fff;
        margin:25px 0;
    }
    h5{
        color:#427CFF;
        background-color: #fff;
        padding:10px 35px;
        border-radius: 20px;
        font-size: 18px;
        width: fit-content;
        margin:0 auto;
    }
}

@media(max-width:1401px){
    .apps-page .banner{
        h1{
            font-size: 32.5px;
        }
        p{
            font-size: 15px;
            margin:20px 0;
        }
        h5{
            padding:10px 30px;
            font-size: 15px;
        }
    }
}

@media(max-width:767px){
    .apps-page .banner{
        padding:70px 7.5%;
        h1{
            font-size: 20px;
        }
        p{
            font-size: 14px;
            margin:15px 0;
        }
        h5{
            padding:9px 25px;
            border-radius: 20px;
            font-size: 14px;
        }
    }
}